.slick-arrow.slick-prev {
  width: 40px;
  height: 40px;
  background-image: url("../../assets/arrowPrew.png");
  background-repeat: no-repeat;
  position: relative;
  left: 15px;
  top: 230px;
  z-index: 1;
  transition: all 0.3s ease;
}

.slick-arrow.slick-next {
  width: 40px;
  height: 40px;
  background-image: url("../../assets/arrowNext.png");
  background-repeat: no-repeat;
  position: relative;
  float: right;
  right: 15px;
  z-index: 1;
  bottom: 210px;
  transition: all 0.3s ease;
}

.slick-arrow.slick-prev:hover {
  background-image: url("../../assets/arrowHover.png");
}

.slick-arrow.slick-next:hover {
  background-image: url("../../assets/arrowHoverNext.png");
}

.slick-prev:before {
  content: "";
}

.slick-next:before {
  content: "";
}

.slick-list {
  width: 100%;
  height: 100%;
  max-width: 1410px;
  max-height: 400px;
}

.slider-section {
  margin-top: 30px;
}

.slick-dots button {
  background-color: #C2C4D2 !important;
  width: 3px !important;
  height: 3px !important;
  border-radius: 5px !important;
  transition: all 0.5s ease;
}

.slick-dots {
  bottom: -40px;
}

.slick-active button {
  width: 6px !important;
  background-color: #F56E1E;
}

.slick-dots button:hover {
  background-color: #F56E1E !important;
}

.slick-dots li.slick-active button {
  width: 20px !important;
  background-color: #F56E1E !important;
  margin-left: -3px;
}

.slick-dots li button:before {
  font-size: 0px;
}

.news-card {
  height: 400px;
  width: 450px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.news-card .news-img {
  width: 450px;
}

.news-card h3 {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  margin-top: 15px;
}

.slider-card.news {
  display: flex !important;
  justify-content: space-between;
}

.news-date {
  font-size: 12px;
  font-weight: 400;
  color: #A0A3BC;
  display: flex;
  height: 17px;
  align-items: center;
}

.news-date img {
  width: 17px;
}

.news-date span {
  margin-left: 10px;
}

.news-message {
  position: absolute;
  top: 10px;
  margin-left: 10px;
  padding: 7px 20px;
  background-color: #F56E1E;
  border-radius: 8px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  width: 100px;
}

.slider-section.news {
  margin-top: -30px;
}/*# sourceMappingURL=slider.css.map */