.more{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    background-color: white;
    .more-button{
        display: flex;
        align-items: center;
        position: relative;
        z-index: 1;
        cursor: pointer;
        .hide{
            position: absolute;
            opacity: 0;
            transition: all 0.3s ease;
            font-size: 3px;
            font-weight: 500;
            cursor: pointer;
        }
        .show{
            opacity: 1;
            transition: all 0.3s ease;
            width: 60px;
            font-size: 14px;
            font-weight: 500;
            cursor: pointer;
        }
        .down{
            transition: all 0.3s ease;
            position: absolute;
            margin-left:60px;
        }
        .up{
            rotate:-180deg;
            transition: all 0.3s ease;
            position: absolute;
            margin-left:60px;
        }
    }
}
