.modal {
  animation-duration: 1s;
  animation-name: apperance;
  margin: 0;
  background: yellow;
  position: absolute;
  top: 45%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  width: 390px;
  background-color: #FFF;
  border-radius: 16px;
  z-index: 40;
  display: flex;
  flex-direction: column;
  padding: 20px 30px 30px 30px;
}

.modal h3 {
  color: #4E4B66;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

@keyframes apperance {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
  20% {
    opacity: 0%;
  }
}
